import React,{useState,useEffect} from 'react';
import {Form, Input,message, Modal, Select,Table, DatePicker} from 'antd';

import {UnorderedListOutlined, AreaChartOutlined,EditOutlined,DeleteOutlined} from '@ant-design/icons'
import Layout from '../components/layout/Layout';
import axios from "axios";
import Spinner from './../components/Spinner';
import moment from "moment";
import Analytics from "../components/Analytics" ;

const {RangePicker}= DatePicker;
const HomePage = () => {
  const [showModal,setShowModal]=useState(false);

  const [loading,setLoading]=useState(false);
  const [allTransaction,setAllTransaction]=useState([]);
   const [frequency,setFrequency] =useState();
   const [selectedDate,setSelectDate]=useState([]);
   const [type,setType] = useState('all')
   const [viewData,setViewData]=useState('table')
   const [editable,setEditable]=useState(null);
  const columns = [
    {
      title: 'Data',
      dataIndex: 'date',
      render : (text) => <span>{moment(text).format('YYYY-MM-DDDD')}</span>
    },
    {
      title: 'Amount',
      dataIndex: 'amount'
    },
    {
      title: 'Type',
      dataIndex: 'type'
    },
    {
      title: 'Category',
      dataIndex: 'category'
    },
    {
      title: 'Reference',
      dataIndex: 'reference'
    },
    {
      title: "Actions",
      render: (text,record) => (
        <div>
          <EditOutlined onClick={()=>
            {
              setEditable(record)
              setShowModal(true)
            }
          }/>
          <DeleteOutlined className='mx-2'
        
          onClick={()=>{

          handleDelete(record);
           } }
          
          />
            
         
        </div>
      )
      
    },
  ]
  
  
  const getAllTransaction = async ()=>{
    try {
      const user=JSON.parse(localStorage.getItem('user'))
      setLoading(true);
      const res=await axios.post('api/v1/transactions/get-transaction',{userid: user._id})
      setLoading(false)
      setAllTransaction(res.data);

    }
    catch(error){
      console.log(error);
      message.error('Fetch Issue With Transaction')
    }
  };
  useEffect(()=>{
    const getAllTransaction = async()=>{
      try{
        const user=JSON.parse(localStorage.getItem('user'))
        setLoading(true)
        const res=await axios.post('api/v1/transactions/get-transaction',{userid: user._id})
        setLoading(false)
        setAllTransaction(res.data);
  
      }
      catch(error){
        console.log(error);
        message.error('Fetch Issue With Transaction')
      }
    };
getAllTransaction();
  },[frequency,selectedDate]);
   const handleDelete=async(record)=>{
    try{
      setLoading(true)
await axios.post("api/v1/transactions/delete-transaction",{transactionId: record._id})
    setLoading(false)
    message.success("Transaction Succesfully Deleted");
}
    catch(error){
setLoading(false)
console.log(error)
message.error('unable to delete')
    }
    
   };
    const handleSubmit = async(values) =>{
            try{
                const user= JSON.parse(localStorage.getItem('user'))
                setLoading(true)
                
               if(editable){
                await axios.post("/transactions/edit-transaction",{
                  payload:{
                   ...values,
                   userId: user._id
                  },
                  transactionId: editable._id
                 });
                 setLoading(false);
                 message.success("Transaction Updated Succesfully");
               }
               else{
                await axios.post("api/v1/transactions/add-transaction",{
                  ...values,
                  userid: user._id,

                });
                setLoading(false);
                message.success("Transaction Added Succesfully")
               }
                setShowModal(false);
                setEditable(null);
            }
            catch(error){
                  setLoading(false)
                  message.error('Failed to add transaction')
            }
    };
  return (
      
      <Layout>
       {loading && <Spinner />   } 
        <div className='filters'>
           <div>Select filters
            <h6>Select Frequency</h6>
            <Select value ={frequency} onChange={(values)=>setFrequency(values)}>
              <Select.Option value="7">LAST 1 Week</Select.Option>
              <Select.Option value="30">LAST 1 Month</Select.Option>
              <Select.Option value="365">LAST 1 Year</Select.Option>
              <Select.Option value="custom">Custom</Select.Option>
            </Select>
            {frequency === 'custom' &&
             (<RangePicker value={selectedDate} onChange={(values)=>setSelectDate(values)} 
            />)}
           </div>
           <div className='switch-icons'>
            <UnorderedListOutlined
            className='mx-2'
            onClick={()=>setViewData("table")}
            />
            <AreaChartOutlined
            className='mx-2'
            
              onClick={()=>setViewData("table")} 
            />
              <button className='btn btn-primary' onClick={()=>setShowModal(true)}>Add New</button>
           </div>

        </div>
        <div className='content'></div>
        <Modal 
        title={editable ? 'Edit Transaction' : 'Add Transaction'} 
        open={showModal} 
        
        
        onCancel={()=>setShowModal(false)}
        footer = {false} >

<Form layout = "vertical" onFinish={handleSubmit} initialValues={editable}>
<Form.Item label="Amount" name="amount">
<Input type='text'/>
</Form.Item>
<Form.Item label="type" name="type">
<Select>
  <Select.Option value="Income">Income</Select.Option>
  <Select.Option value="expense">Expense</Select.Option>
</Select>
</Form.Item>
<Form.Item label="Category" name="category">
<Select>
  <Select.Option value="Salary">Salary</Select.Option>
  <Select.Option value="Tip">Tip</Select.Option>
  <Select.Option value="Project">Project</Select.Option>
  <Select.Option value="Food">Food</Select.Option>
  <Select.Option value="Movie">Movie</Select.Option>
  <Select.Option value="Bills">Bills</Select.Option>
  
  <Select.Option value="Medical">Medical</Select.Option>
  </Select>
</Form.Item>
<Form.Item label="Date ">
  <Input type="date"/>
</Form.Item>
<Form.Item label="Reference" name="reference">
  <Input type="text"/>
</Form.Item>
<Form.Item label="Description" name="description">
  <Input type="text"/>
</Form.Item>
<div className='d-flex justify-content-end' >
  <button type='submit'className='btn btn-primary'>SAVE</button>
</div>
</Form>
          
        </Modal>
      </Layout>
     
      
    );
};
export default HomePage