import React from "react";
import { Progress } from 'antd' ;
const Analytics =({allTransaction})=>{
    const categories = ['Salary','Tip','Project','Food','Movie','Bills']
    const totalTransaction =allTransaction.ength
    const totalIncometransactions = allTransaction.fiter(transaction => transaction.type ==='income')
    const totalExpensetransactions = allTransaction.fiter(transaction => transaction.type ==='expense')
    const totalIncomePercent=(totalIncometransactions.length/totalTransaction)*100;
  const totalExpensePercent =( totalExpensetransactions.length/totalTransaction)*100;
  const totalTurnover = allTransaction.reduce((acc,transaction)=>
    acc+transaction.amount,0
  );
  const totalIncomeTurnover = allTransaction.filter(transaction=>transaction.type === 'income').reduce((acc,transaction)=>acc+transaction.amount,0)
    
  const totalExpenseTurnover = allTransaction.filter(transaction => transaction.type === "expense"

  ).reduce((acc,transaction)=>acc+transaction.amount,0 );
 
 const totalIncomeTurnoverPercent = (totalIncomeTurnover/totalTurnover)*100
 const totalExpenseTurnoverPercent = (totalExpenseTurnover/totalTurnover)*100
 return (
        <>
        <div className="row m-3">
          <div className="col-md-4"  >
            <div className="card">
                 <div className="card-header">
                 Total Transaction: {totalTransaction}
                 </div>
            <div className="card-body">
                 <h5 className="text-success">Income :{totalIncometransactions.length}</h5>
                <h5 className="text-danger"> Expense: {totalExpensetransactions.length} </h5>
           <div>
            <Progress type="circle" strokeColor={'green'} className="mx-2" 
            percent={totalIncomePercent.toFixed(0)}/>
             <Progress type="circle" strokeColor={'red'} className="mx-2" 
            percent={totalIncomePercent.toFixed(0)}/>
           </div>
           
            </div>
            </div>
          </div>
        </div>
        <div className="row mt-3">
            <div className="col-md-5">
               <h4> Category wise Income</h4>
               {
                categories.map(category =>{
                    const amount = allTransaction.filter(transaction => transaction.type === 'income' && transaction.category === category ).reduce((acc,transaction)=>
                    acc+transaction.amount,0);
                    return(
                        <div className="card">
                            <div className="card-body">
                                <h5>{categories}</h5>
                                <Progress percent={((amount/totalIncomeTurnover)*100).toFixed(0)} />
                                </div>
                        </div>
                    )
                })
               }
            </div>
        </div>
        </>
    );
};

export default Analytics;